.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
  padding: 20px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.social-links {
  margin-top: 20px;
  
  a {
    color: #61dafb;
    margin: 0 10px;
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
  }
} 